function hashGenerator (s) {
    let sWithDate = `${s}${Math.round(
      new Date().getTime() / 1000
    )}`;
  
    return sWithDate.split("").reduce(function(a, b) {
      a = (a << 5) - a + b.charCodeAt(0);
      return a & a;
    }, 0).toString();
  };

  export {hashGenerator}


