/*eslint-disable no-unused-vars*/
import React, { Component } from "react";
/*eslint-enable no-unused-vars*/
import Group from "./Group";
import Panel from "muicss/lib/react/panel";
import Appbar from "muicss/lib/react/appbar";
import Container from "muicss/lib/react/container";
import { Button, Icon } from "react-mdl";
import AddGroup from "./AddGroup";
import { Link } from "react-router-dom";

export default class Groups extends React.Component {
  constructor(props) {
    super(props);

    this.state = { groups: [] };
    this.handleGroupAdded = this.handleGroupAdded.bind(this);
    this.handleDoneClicked = this.handleDoneClicked.bind(this);
    this.registerDone = this.registerDone.bind(this);
  }
  componentWillMount() {
    this.props.fetch().then(groups => {
      this.setState(prevState => ({
        groups: groups
      }));
    });
  }

  handleGroupAdded(group) {
    this.props.add(group);
    this.setState(prevState => ({
      groups: prevState.groups.concat(group)
    }));
  }

  handleGroupDeleted(id) {
    this.props.delete(id);
    let gid = this.state.groups.find(group => {
      return group.id === id;
    });
    this.setState(prevState => ({
      groups: prevState.groups.splice(gid, 1)
    }));
  }

  registerDone(f) {
    this.setState({
      doneHandler: f
    });
  }

  handleDoneClicked() {
    this.state.doneHandler();
  }

  render() {
    let groupList = [];
    if (this.state.groups !== undefined && this.state.groups.length > 0) {
      groupList = this.state.groups.map(group => {
        let url = "/group/" + group.id;
        return (
          <Link to={url} key={group.id}>
            <Panel>
              {/* <span
                onClick={() => {
                  this.props.onClicked(group.id);
                }}
              > */}
              <Group
                id={group.id}
                name={group.name}
                items={group.items}
              />
              <span>
                <Button onClick={this.handleRemove}>
                  <Icon id={this.state.id} name="delete" />
                </Button>
              </span>
            </Panel>
          </Link>
        );
      });
    }
    return (
      <Container>
        <Appbar>
          <Icon name="done" onClick={this.handleDoneClicked} />
        </Appbar>
        <AddGroup
          onGroupAdded={this.handleGroupAdded}
          register={this.registerDone}
        />
        <div className="groups">{groupList}</div>
      </Container>
    );
  }
}



