/*eslint-disable no-unused-vars*/
import React, { Component } from "react";
/*eslint-enable no-unused-vars*/
import { Textfield } from "react-mdl";
import { hashGenerator } from "./hash";

export default class AddItem extends React.Component {
  constructor(props) {
    super(props);
    this.props.register(this.submit(this));
    this.state = { id: props.id, nameValue: "", phoneValue: "" };
    this.handleNameChanged = this.handleNameChanged.bind(this);
    this.handlePhoneChanged = this.handlePhoneChanged.bind(this);
  }
  submit(self) {
    return function() {
      let item = {
        id: hashGenerator(`${self.state.phoneValue}${self.state.nameValue}`),
        name: self.state.nameValue,
        phone: self.state.phoneValue
      };
      self.props.onItemAdded(item);
      self.setState({ nameValue: "", phoneValue: "" });
    };
  }
  handleNameChanged(e) {
    this.setState({
      nameValue: e.target.value
    });
  }
  handlePhoneChanged(e) {
    this.setState({
      phoneValue: e.target.value
    });
  }

  render() {
    return (
      <form>
        <Textfield
          onChange={this.handleNameChanged}
          label="Name..."
          value={this.state.nameValue}
          style={{ width: "200px" }}
        />
        {"  "}
        <Textfield
          onChange={this.handlePhoneChanged}
          label="Phone..."
          value={this.state.phoneValue}
          style={{ width: "200px" }}
        />
        {"  "}
      </form>
    );
  }
}



