/*eslint-disable no-unused-vars*/
import React, { Component } from "react";
/*eslint-enable no-unused-vars*/
import { Textfield } from "react-mdl";
import {hashGenerator} from "./hash";

export default class AddGroup extends React.Component {
  constructor(props) {
    super(props);

    this.state = { nameValue: "" };
    this.props.register(this.submit(this));
    this.handleNameChanged = this.handleNameChanged.bind(this);
  }
  handleNameChanged(e) {
    let input = e.target.value;
    if (input > 144) {
      input.substring(1, 144);
    }
    this.setState({
      nameValue: input
    });
  }
  submit(self) {
    return function() {
      let group = {
        id: hashGenerator(self.state.nameValue),
        name: self.state.nameValue,
        items: []
      };
      self.props.onGroupAdded(group);
      self.setState({ nameValue: "" });
    };
  }

  render() {
    return (
      <form>
        <Textfield
          onChange={this.handleNameChanged}
          label="New Group..."
          value={this.state.nameValue}
        />
      </form>
    );
  }
}




