/*eslint-disable no-unused-vars*/
import React, { Component } from "react";
/*eslint-enable no-unused-vars*/
import Container from "muicss/lib/react/container";

class Group extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props.id,
      name: this.props.name,
      items: this.props.items
      //showItems: this.props.show
      //handleAdded: this.props.onAdded,
      //onDeleted: this.props.onChange,
    };
    this.handleItemAdded = this.handleItemAdded.bind(this);
    this.handleItemRemoved = this.handleItemRemoved.bind(this);
  }

  // componentWillMount() {
  //   fetch("https://jsonplaceholder.typicode.com/3users") // returns a promise object
  //     .then(result => result.json()) // still returns a promise object, U need to chain it again
  //     //.then(() => sleep(5))
  //     .then(items => this.setState({ items }))
  //     .catch(() => {
  //       console.log("fail to get data");
  //       const items = [];
  //       this.setState({ items });
  //     });
  // }

  handleRemoved(e) {
    console.log(`remove`);
    e.preventDefault();
    e.stopPropagation();
  }
  handleItemRemoved(e) {
    let ti = this.state.items;
    const f = ti.find(o => {
      return o.id === e;
    });
    const ind = ti.indexOf(f);

    if (ind === -1) {
      return;
    }
    ti.splice(ind, 1);
    console.log(ti);
    this.props.onChanged(this.state.id, this.state.name, ti);
    this.setState({ items: ti });
  }

  handleItemAdded(e) {
    let items = this.state.items.slice();
    items.push(e);
    this.props.onChanged(this.state.id, this.state.name, items);
    this.setState(prevState => ({
      items: prevState.items.concat(e)
    }));
  }

  componentWillMount() {
    if (
      this.props.match !== undefined &&
      this.props.match.params.id !== undefined
    ) {
      this.setState({ id: this.props.match.params.id });
    }
  }

  render() {
    return (
      <Container>
        <div>
          {this.state.id} {this.state.name} {/*({this.state.items.length}) */}
        </div>
      </Container>
    );
  }
}

export default Group;



